import React from "react";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import {
  Typography,
  Grid,
  Box,
  TextField,
  Avatar,
  Button,
  CssBaseline,
  Container,
  Collapse,
} from "@material-ui/core";

import Copyright from "../Components/Copyright";
import { useState } from "react";
import ErrorAlert from "../Components/ErrorAlert";
import utils from "../utils";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignUp() {
  const classes = useStyles();
  const [pass1, setPass1] = useState("");
  const [pass2, setPass2] = useState("");
  const [error1, setError1] = useState(false);
  const [error2, setError2] = useState(false);

  const [invalid, setInvalid] = useState(false);
  const history = useHistory();

  return (
    <Container component="main" maxWidth="sm">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar
          className={classes.avatar}
          alt="nitt-logo"
          src="/static/logos/nitt.png"
          style={{ height: "10vh", width: "10vh" }}
        />
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <Collapse in={!!invalid}>
          <ErrorAlert
            errorObj={invalid}
            handleClose={() => setInvalid(false)}
          />
        </Collapse>
        <form
          className={classes.form}
          onSubmit={(e) => {
            e.preventDefault();
            const name = e.target[0].value;
            const email = e.target[2].value;
            const password = e.target[4].value;
            const password2 = e.target[6].value;
            utils
              .register({ name, email, password, password2 })
              .then((data) => {
                if (data.user1) {
                  setInvalid({
                    msg: data.Response,
                    severity: "success",
                  });

                  setTimeout(() => history.replace("/"), 2000);
                } else {
                  setInvalid({
                    msg: data.Response,
                    severity: "warning",
                  });
                }
              })
              .catch((err) => {
                console.log(err);
                setInvalid({
                  msg: "Something went wrong!",
                  severity: "error",
                });
              });
          }}
        >
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="name"
                name="name"
                variant="outlined"
                required
                fullWidth
                id="name"
                label="Name"
                autoFocus
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                type="email"
                label="Email Address"
                name="email"
                autoComplete="email"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                helperText={
                  error2 && "Password should be atleast 6 characters long"
                }
                error={error2}
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                value={pass1}
                onChange={(e) => {
                  setPass1(e.target.value);
                  setError2(e.target.value.length < 6);
                }}
                autoComplete="current-password"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                error={error1}
                helperText={error1 && "Passwords do not match"}
                variant="outlined"
                required
                fullWidth
                name="password2"
                label="Confirm Password"
                type="password"
                id="password2"
                value={pass2}
                onChange={(e) => {
                  setPass2(e.target.value);
                  setError1(!(e.target.value === pass1));
                }}
                autoComplete="current-password"
              />
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            disabled={error1 || error2}
          >
            Sign Up
          </Button>
          <Grid container justify="flex-end">
            <Grid item>
              <Link to="/" variant="body2">
                Already have an account? Sign in
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={5}>
        <Copyright />
      </Box>
    </Container>
  );
}
