import { useState, useContext } from "react";
import {
  Card,
  CardContent,
  Divider,
  Typography,
  CardActions,
  Grid,
  TextField,
  Button,
  LinearProgress,
  Slide,
} from "@material-ui/core";
import { useSnackbar } from "notistack";
import utils from "../utils";
import { useHistory } from "react-router-dom";
import SessionContext from "../SessionContext";

const Puzzle = (props) => {
  const { enqueueSnackbar } = useSnackbar();
  const [completed, setComplete] = useState(false);
  const { puzzle } = props;
  const history = useHistory();
  const { sessionData, setSessionData } = useContext(SessionContext);

  return (
    <Card>
      <CardContent>
        {puzzle ? (
          <>
            <Divider variant="middle" />
            <br />

            <Typography variant="subtitle1" component="p">
              {puzzle}
            </Typography>

            <br />
            <Divider variant="middle" />

            <CardActions>
              <Grid
                container
                spacing={3}
                justify="flex-start"
                alignItems="flex-start"
              >
                <Grid item xs={4}>
                  <form
                    noValidate
                    autoComplete="off"
                    onSubmit={(e) => {
                      e.preventDefault();
                      let ans = e.target[0].value;
                      utils
                        .checkPuzzleAnswer(ans, props.id)
                        .then((res) => {
                          if (res.status) {
                            setComplete(true);
                            props.onSolved();
                          } else {
                            enqueueSnackbar("Wrong answer", {
                              anchorOrigin: {
                                vertical: "top",
                                horizontal: "center",
                              },
                              variant: "error",
                              TransitionComponent: Slide,
                            });
                          }
                        })
                        .catch((err) => {
                          const error = err.response;
                          if (error.status === 403) {
                            utils.logout();
                            setSessionData(null);
                            history.push("/end");
                          } else {
                            enqueueSnackbar("Something went wrong !!", {
                              anchorOrigin: {
                                vertical: "top",
                                horizontal: "center",
                              },
                              variant: "error",
                              TransitionComponent: Slide,
                            });
                          }
                        });
                    }}
                  >
                    <TextField
                      variant="outlined"
                      margin="normal"
                      required
                      fullWidth
                      id="ans"
                      label="Answer"
                      name="ans"
                      autoFocus
                    />
                    <Button
                      disabled={completed}
                      type="submit"
                      variant="contained"
                      style={{
                        backgroundColor: "#9fe6a0",
                      }}
                      size="medium"
                    >
                      Check Answer
                    </Button>
                  </form>
                </Grid>
              </Grid>
            </CardActions>
          </>
        ) : (
          <LinearProgress />
        )}
      </CardContent>
    </Card>
  );
};

export default Puzzle;
